import React from "react";
import { Route, Routes } from "react-router-dom";

const Scripts = React.lazy(() => import("../scripts/scripts"));
const Script = React.lazy(() => import("../scripts/script"));
const Jobs = React.lazy(() => import("../jobs/jobs"));
const Job = React.lazy(() => import("../jobs/job"));
const Schedules = React.lazy(() => import("../schedule/schedules"));
const Triggers = React.lazy(() => import("../ui/triggers/triggers"));
const Terminals = React.lazy(() => import("../terminal/terminals"));
const Terminal = React.lazy(() => import("../terminal/terminal"));
const Hotkeys = React.lazy(() => import("../hotkeys/hotkeys"));

export default function AutomationsRoute() {
  return (
    <Routes>
      <Route path="scripts" element={<Scripts />} />
      <Route path="jobs" element={<Jobs />} />
      <Route path="jobs/failed" element={<Jobs state={"failed"} />} />
      <Route path="jobs/running" element={<Jobs state={"running"} />} />
      <Route path="jobs/waiting" element={<Jobs state={"waitingOnFeedback"} />} />
      <Route path="schedules" element={<Schedules />} />
      <Route path="hotkeys" element={<Hotkeys />} />
      <Route path="triggers" element={<Triggers />} />
      <Route path="jobs/:id" element={<Job />} />
      <Route path="scripts/*" element={<Script />} />
      <Route path="terminals" element={<Terminals />} />
      <Route path="terminal/:id" element={<Terminal />} />
    </Routes>
  );
}
